            shareText:"商品详情shareText",
            imageUrl:"https://ss1.baidu.com/6ONXsjip0QIZ8tyhnq/it/u=2671670863,3877857355&fm=58&w=258&h=258&img.JPEG"
          },
          shareType:0,
        // },
        buyNum:2,
        currentStock:9,
        popupData:{
           imgUrl:'../../static/detalisImg.png',
           price:'99',
           stock:'999',
           num:'1'
        },
        ifSelectType:true,
        showMask:false,
        showDetailsState:false,
        imgUrls: [
        	"../../static/detalisImg.png",
        	"../../static/detalisImg.png",
        	"../../static/detalisImg.png"
        ],
        selectType:[],
        unfoldDetailsShow:true,
        goodsCollectText:'收藏',
        shopCollectText:'收藏',
        goodsData:{
          cartGoodsNum:'2',
          collectShopShow:false,
          goodsCollectShow:false,
          name:'进口葡萄籽精华胶囊时尚',
          price:'899.0',
          originalCost:'999',
          coupon:'100',
          commission:'36.12',
          express:"0.00",
          sales:'521',
          site:'河南郑州',
          stock:"9999",
          parameter:"",
          spec:[
            {
              size:'300mg/瓶',
              imgUrl:'../../static/goodsDetails.png',
              color:'',
              price:'999.0',
              stock:"99",
              checked:1,
              id:'1'
            },
            {
            	size:'300mg/瓶',
            	imgUrl:'../../static/goodsDetails.png',
            	color:'',
              price:'999.0',
              stock:"99",
              checked:1,
              id:'2'
            }
          ],
          describe:"4.7高",
          serve:'4.7高',
          expressServe:'4.7高',
          particulars:[
            '../../static/goodsDetails.png',
            '../../static/goodsDetails.png',
            '../../static/goodsDetails.png',
          ]
        },
        recommendListData:[
        	{
        		id:1,
        		goodsName:'新西兰原装美容养颜葡萄...',
        		imgUrl:'../../../static/tuijian.png',
        		discounts:'佣金',
        		discountsIntro:'63.21',
        		sales:'1234',
        		price:'99.9',
        		originalPrice:'168.9',
        		hrefUrl:''
        	},
        	{
        		id:2,
        		goodsName:'新西兰原装美容养颜葡萄...',
        		imgUrl:'../../../static/tuijian.png',
        		discounts:'佣金',
        		discountsIntro:'63.21',
        		sales:'1234',
        		price:'99.9',
        		originalPrice:'168.9',
        		hrefUrl:''
        	},
        	
        ],
        toastText:"请选择商品大小"
			}
		},
    onShareAppMessage() {
    	return {
    		title: this.shareData.shareText ? this.shareData.shareText : "欢迎体验健康联盟",
    		path: '/pages/healthyMall/goodsDetails',
    	}
    },
		methods: {
      // 分享
      shareGoods(){
        console.log('点击分享')
        this.maskShow = true
        this.popupShow = true
      },
      collectGoods(status){
        if(status===1){
          console.log('点击收藏')
          this.goodsData.goodsCollectShow = false
          this.goodsCollectText = '收藏'
        }else{
          console.log('点击取消收藏')
          this.goodsData.goodsCollectShow = true
          this.goodsCollectText = '已收藏'
        }
      },
      collectShop(status){
        if(status===1){
        	console.log('点击收藏')
        	this.goodsData.collectShopShow = false
        	this.shopCollectText = '收藏'
        }else{
        	console.log('点击取消收藏')
        	this.goodsData.collectShopShow = true
        	this.shopCollectText = '已收藏'
        }
      },
      unfoldDetails(){
        console.log('展开宝贝详情')
        if(this.unfoldDetailsShow){
          this.unfoldDetailsShow = false
        }else{
          this.unfoldDetailsShow = true
        }
      },
      getCart(){
        uni.navigateTo({
        	url:"/pages/healthyMall/shoppingCart"
        })
      },
      getGoodsType(status){
        if(status==='type'){
          console.log('选择商品规格')
          this.showDetailsState = true
          this.showMask = true
        }else if(status==='cart'){
          console.log('点击加入购物车')
          if(this.selectType.length===0){
            this.showDetailsState = true
            this.showMask = true
          }else{
            console.log('提示加入购物车')
          }
        }else if(status==='buy'){
          console.log('点击购买')
          if(this.selectType.length===0){
          	this.showDetailsState = true
            this.showMask = true
          }else{
            // 跳转到订单详情页面
            uni.navigateTo({
            	url:'/pages/healthyMall/settleAccount'
            })
          }
        }else if(status==='buyPopup'){
          console.log('在弹框中点击购买')
          if(this.selectType.length===0){
            this.toastTap()
          }else{
            // 购买请求接口成功后跳转页面
            uni.navigateTo({
            	url:'/pages/healthyMall/settleAccount'
            })
          }
        }else{
          if(this.selectType.length===0){
          	this.toastTap()
          }else{
            // 请求加入购物车接口成功后给提示
            this.toastText='加入购物车成功！'
            this.toastTap()
          }
          console.log('在弹框中点击购物车')
        }
      },
      hideMask(){
        console.log('遮罩层显示隐藏')
        this.showMask = false
        this.showDetailsState = false
      },
      numberUpdate(value){
        console.log(value)
        this.buyNum = value
      },
      toastTap: function () {
      	uni.showToast({
      		title: this.toastText
      	})
      },
      inABatch(){
        console.log('点击换一批推荐商品')
      },
      getPopupType(item){
        console.log(item)
        if(item.checked===0){
          console.log('已经为选中')
        }else{
          console.log('设置为选中')
          let h = this.goodsData.spec.length
          let id = item.id
          for(let i = 0;i<h;i++){
            if(id === this.goodsData.spec[i].id){
              this.goodsData.spec[i].checked=0
              this.selectType=[]
              this.selectType.push(this.goodsData.spec[i].id)
            }else{
              this.goodsData.spec[i].checked=1
            }
          }
        }
      }
		}
	}



